

.contact-section {
    display: grid;
    justify-self: center;
    width: 90%;
    max-width: 960px;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: auto;
    grid-template-areas:
        "contact-line contact-line"
            "contact-header contact-header"
                "contact-form contact-info";
    border-top: 1px solid lightgray;
}

.contact-header {
    grid-area: contact-header;
    padding: 10px;
}

.contact-info {
    grid-area: contact-info;
    padding: 10px;
}

.contact-form {
    grid-area: contact-form;
    padding: 10px;
    padding-right: 30px;
}

.contact-line {
    grid-area: contact-line;
    -ms-flex-item-align: center;
        align-self: center;
    justify-self: center;
    height: 30px;
    width: 80px;
    margin-bottom: 5px;
}

.contact-line hr {
    border-top: 8px solid red;
}

.contact-submit {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
}



