


.counter-overlay-container {
    display: grid;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center;

    background: url("surf.jpg") no-repeat center center fixed;
    background-position: 80% 80%;
    height: 100vh;
    width: 100vw;
    background-size: cover;
    background-repeat: no-repeat;

    grid-template-columns: 1fr 4fr 1fr;
    grid-template-rows: auto;
    grid-template-areas:
        "header header header"
            ". main ."
            "footer footer footer";
}



.headline {
    margin-left: 0px;
    margin-right: 0px;
}



.timer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 50%;
    min-width: 300px;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    justify-self: center;
    /* color: white; */
}

.main {
    grid-area: main;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background-color: rgba(241,235,243,0.3);
    padding: 5px;
    color: black;
}



.synthetik-logo { 
     width: 80px;
}

.contact {
    color: black;
}

.synthetik-header {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px;
    width: 100%;
    
}



